<template>

  <div id="container">
    <header ref="homePage">
      <transition name="fade">
        <h2 v-if="headerShow">You're uinique, nothing can replace you.</h2>
      </transition>
    </header>

    <div class="box" style="background-color: yellow">
      <transition name="fade">
        <h3 v-if="isShows[0].isShow">You're uinique, nothing can replace you.</h3>
      </transition>
    </div>

    <div class="box" style="background-color: sienna">
      <transition name="fade">
        <h3 v-if="isShows[1].isShow">You're uinique, nothing can replace you.</h3>
      </transition>
    </div>

    <div class="box" style="background-color: rosybrown">
      <transition name="fade">
        <h3 v-if="isShows[2].isShow">You're uinique, nothing can replace you.</h3>
      </transition>
    </div>

    <div class="box" style="background-color: gray">
      <transition name="fade">
        <h3 v-if="isShows[3].isShow">You're uinique, nothing can replace you.</h3>
      </transition>
    </div>

  </div>

</template>

<script>
  export default {
    name: "Home",
    data() {
      return {
        clientHeight: 0,
        headerShow: false,
        isShows: [
          {isShow: false},
          {isShow: false},
          {isShow: false},
          {isShow: false}
        ],
        isBottom: false
      }
    },

    beforeCreate() {
      this.$Loading.start();
    },
    created() {
      this.$Loading.finish();
    },

    mounted() {
      // 获取浏览器可视区域高度
      this.clientHeight = document.documentElement.clientHeight;
      window.onresize = function temp() {
        this.clientHeight = document.documentElement.clientHeight;
      };

      //  让header中的元素动态显示
      this.headerShow = true;

      //  useCapture——true: 外部元素的事件先触发
      window.addEventListener('scroll', this.showBox, true);
    },
    destroyed() {
      window.removeEventListener('scroll', this.showBox)
    },

    methods: {
      //动态修改样式
      changeFixed(clientHeight) {
        this.$refs.homePage.style.height = clientHeight + 'px';
      },

      //  动态显示boxes
      showBox: function () {
        console.log('showBox');

        //  首先判断是否触底
        let view = this.clientHeight;
        let height = document.body.clientHeight;
        let scroll = document.documentElement.scrollTop;

        if (!this.isBottom && scroll + view < height) {
          this.fadeBox();
        } else {
          this.isBottom = true;
        }
      },

      fadeBox: function () {
        const boxes = document.querySelectorAll('.box');
        let length = boxes.length;

        for (let index = 0; index < length; index++) {
          let element = boxes[index];
          const viewPortHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
          const top = element.getBoundingClientRect() && element.getBoundingClientRect().top;

          if (top <= viewPortHeight - 100) {
            this.isShows[index].isShow = this.isShows[index].isShow ? true : true;
          }
        }
      }
    },

    watch: {
      // 如果 `clientHeight` 发生改变，这个函数就会运行
      clientHeight: function () {
        this.changeFixed(this.clientHeight);
      }
    }
  }

</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    text-align: center;
  }

  header {
    width: 100%;
    font-size: 5rem;
    text-align: center;
    background-image: url("../assets/wallpaper.jpg");

    position: relative;
    top: 0;
    left: 0;
    zoom: 1;
    background-color: #fff;
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-background-size: cover;
    -o-background-size: cover;
    background-position: center 0;
  }

  h2 {
    color: white;
    font-family: "Nunito", Arial, sans-serif;
    font-size: 60px;
    font-weight: 400;
    line-height: 1.3;
  }

  h3 {
    color: white;
    font-family: "Nunito", Arial, sans-serif;
    font-size: 40px;
    font-weight: 300;
  }

  #container {
    height: 100%;
    text-align: center;
  }

  .box {
    /*border: 1px solid black;*/
    height: 200px;
    width: 100%;
  }

  .fade-enter, .fade-leave-to {
    opacity: 0;
    transform: translateY(50px);
  }

  .fade-enter-to, .fade-leave {
    opacity: 1;
  }

  .fade-enter-active, .fade-leave-active {
    transition: all 0.8s;
  }

</style>



